import { Component, OnInit } from '@angular/core';

import { trigger, transition, style, animate, group, state } from '@angular/animations';


@Component({
  selector: 'app-anim-demo',
  templateUrl: './anim-demo.component.html',
  styleUrls: ['./anim-demo.component.css'],

  animations:[
    trigger('flyInOut',[state('void',style({transform:'translateX(-100%)',opacity:0})),
      transition('void=>*',[group(
      [animate('2s',style({transform:'translateX(0%)'})),
        animate('2s',style({opacity:1}))] )]),
      
  transition('*=>void',[group(
    [animate('4s',style({transform:'translateX(100%)'})),
      animate('4s',style({opacity:0}))] )])
  ]),
  trigger('flyInOut1',[state('void',style({transform:'translateX(-100%)',opacity:0})),
  transition('void=>*',[group(
  [animate('3s',style({transform:'translateX(0%)'})),
    animate('3s',style({opacity:1}))] )]),
  
  transition('*=>void',[group(
    [animate('3s',style({transform:'translateX(100%)'})),
      animate('3s',style({opacity:0}))] )]
  )
  ]),
  trigger('flyInOut2',[state('void',style({transform:'translateX(-100%)',opacity:0})),
  transition('void=>*',[group(
  [animate('4s',style({transform:'translateX(0%)'})),
    animate('4s',style({opacity:1}))] )]),
  
     transition('*=>void',[group(
      [animate('2s',style({transform:'translateX(100%)'})),
        animate('2s',style({opacity:0}))] )]
  )
  ]),trigger('fade',[state('void',style({opacity:0})),
  transition('void=>*',[group(
  [
    animate('5s',style({opacity:1}))] )])])


]
})
export class AnimDemoComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {

    
setTimeout(() => {  this.toggleDiv() }, 6000);
  }

  showAnim:boolean=true;
  showAnim1:boolean=false;
  toggleDiv(){

    this.showAnim=!this.showAnim;
    setTimeout(() => { console.log() }, 24000);
    this.showAnim1=!this.showAnim1;
  }
  onClick(){
    location.reload();
  }}